body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.add-task-button {
	width:80%;
	padding: 5px;
	background: green;
	color: white;
	font-size: 35px;
	margin: 15px auto;
	border: none;
	position: fixed;	
	top: 0;
	z-index: 1;
	box-shadow: 0px 1px 5px #7d7d7d;
}
.task {
	border: 1px solid black;
	position: relative;
	padding:10px;
	border-radius:2px;
	margin: 10px;

	box-sizing: border-box;
	transition: all 100ms;
}

.task.hovered:before {
	height: calc(100% + 20px);
	position: absolute;
	left: -11px;
	top:-10px;
	width:2px;
	background: blue;
	content:'';
}

.priority {
	position: absolute;
	right:0;
	bottom:0;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	z-index: 2;
}

.redRadio {
	background: red;
}

.yellowRadio {
	background: yellow;
}

.greenRadio {
	background: green;
}


.blueRadio {
	background: blue;
}

.purpleRadio {
	background: purple;
}

.red {
	background: #ffa5a5;
}

.yellow {
	background: #fff9a5;
}

.green {
	background: #bbffa5;
}

.blue {
	background: #a5e1ff;
}

.purple {
	background: #eb98ff;
}

input[type="radio"] {
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	width: 20px;
	height: 20px;
	border-radius: 50%;
	margin: 10px 10px;
	box-shadow: 0 0 5px 1px black;
}

input[type="radio"]:focus {
	outline: none;
}

.delete {
	position: absolute;
	top: 0;
	right: 0;
	height:20px;
	width: 20px;
	padding: 10px;
	font-size: 20px;
	cursor: pointer;
	color: red;
	text-shadow: 0px 0px 20px white;
	z-index:2;
}

.edit {
	padding: 10px;
	font-size: 20px;
	position: absolute;	
	top:0;
	left:0;	
	cursor:pointer;
	z-index:2;
}

.section-title {
	float: left;
	font-weight: bold;
	margin-right: 10px;
}

.section-data {
	

}

.section {
	width: 100%;
	float: left;
	margin: 5px 0;
}

.title {
	margin-top: 0px;
	text-align: center;
	margin-bottom: 5px;
}

.note {
	margin-bottom: 30px;
}

.cover {
	width:100%;
	height: 100%;
	position: absolute;
	top:0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index:1;
}
.edit-window {
	position: fixed;
	width: 60%;
	height: 60%;
	top:20%;
	left:20%;
	border: 1px solid black;
	border-radius: 3px;
	background: #ffffffb5;
	z-index: 5;
}

.formSection {
	padding: 10px;
}

.formSection input {
	padding:10px;
	margin: 5px;
	width:50%;
	border: 1px solid black;
	border-radius: 2px;
	font-size: 16px;
	letter-spacing: 1px;
}

.formSection label {
	display: block;
}

.outer {
	width:100vw;
	height:100vh;
	left:0;
	right:0;
	bottom:0;
	top: 0;
	background: #8080809e;
	position: fixed;
	z-index: 4;
}

.close-button {
	position: absolute;
	top:0;
	right:0;
	width:20px;
	height:20px;
	text-align: center;
	background:red;
	padding: 5px;
	color: white;
	cursor: pointer;
}

#note {
	width:100%;
	float:left;
	height:100px;
	font-size: 16px;
	font-family: inherit;
}
.App {
  width: 80%;
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-top:80px;
  display: grid;
  grid-template-columns:repeat(4, 1fr);
}

@media only screen and (max-width: 1440px) {
  .App {
    grid-template-columns:repeat(3, 1fr);
  }
}

@media only screen and (max-width: 1024px) {
  .App {
    grid-template-columns:repeat(2, 1fr);
  }
}

@media only screen and (max-width: 748px) {
  .App {
    grid-template-columns:1fr;
  }
}
